let userInfo = {}
import send from './send'

let setUserInfo = function(info){
    userInfo = info
}

let setUsername = function(params) {
    userInfo.username = params
}

let setAppList = function(info){
    userInfo.list = info
}

let getUsername = function(){
    send.sendMessage('post', 'http://127.0.0.1:8080/getUsername').then(res=>{
            debugger
            console.log(res)
        })
    
}

let getUserId = function(){
    return userInfo.id
}

let getAppList = function(){
    return userInfo.list
}

export default{
    setUserInfo,
    getUsername,
    getUserId,
    setAppList,
    getAppList,
    setUsername,
}


